.promo__button {
    z-index: 2;
    position: relative;
    margin-top: 70px;
    margin-left: 160px;
    width: 200px;
    height: 50px;
    white-space: nowrap;
    overflow: hidden;
    animation: showDiv 13.6s 1 forwards;
    border: 0;
    background-color: #ffffff;
}

@keyframes showDiv {
    0%,
    99% {
        color: #ffffff;
    }
    100% {
        height: 50px;
    }
}

@media screen and (max-width: 426px) {
    .promo__button {
        width: 100px;
        margin-left: 28vw;
    }
}